/*定义字体大小*/
.font10 {font-size: 10px !important;}
.font12 {font-size: 12px !important;}
.font14 {font-size: 14px !important;}
.font16 {font-size: 16px !important;}
.font18 {font-size: 18px !important;}
.font20 {font-size: 20px !important;}
.font22 {font-size: 22px !important;}
.font24 {font-size: 24px !important;}
.font26 {font-size: 26px !important;}
.font28 {font-size: 28px !important;}
.font30 {font-size: 30px !important;}
.font35 {font-size: 35px !important;}
.font40 {font-size: 40px !important;}
.font45 {font-size: 45px !important;}
.font50 {font-size: 50px !important;}

/*文字显示方式*/
.text-left {text-align: left;}
.text-right {text-align: right;}
.text-center {text-align: center;}
.text-justify {text-align: justify;}

/*文字换行的截断方式*/
.word-break {
	word-wrap: break-word;
	word-break: break-all;
}

/*定义通用行高*/
.line-h-nor {line-height:1 !important;}
.line-h-12 {line-height:1.2 !important;}
.line-h-14 {line-height:1.4 !important;}
.line-h-16 {line-height:1.6 !important;}

/*自定义页面的背景色*/
body { background-color: #EDECF3; }

/*页面的安全宽度，如果是使用固定定位fixed的就需要添加.container*/
body,
.container {
	max-width: 480px;
	min-width: 320px;
	margin: 0 auto;
}

/*当页面有底部导航时在外层元素加这个class保证内容不被底部导航覆盖*/
.k-wrap{padding-bottom: 56px;}

/*行内元素的垂直居中处理，包括图片下方有空白间隙也可以解决*/
.ver-middle {vertical-align: middle;}

/*左右浮动*/
.pull-right {float: right !important;}
.pull-left {float: left !important;}

/*清除浮动*/
.clearfix:before,
.clearfix:after{
    display: table;
    content: " ";
}
.clearfix:after {clear: both;}

/*上外边距*/
.margin-t-5 {margin-top: 5px;}
.margin-t-10 {margin-top: 10px;}
.margin-t-15 {margin-top: 15px;}
.margin-t-20 {margin-top: 20px;}
/*下外边距*/
.margin-b-5 {margin-bottom: 5px;}
.margin-b-10 {margin-bottom: 10px;}
.margin-b-15 {margin-bottom: 15px;}
.margin-b-20 {margin-bottom: 20px;}
/*左外边距*/
.margin-l-0 {margin-left: 0 !important;}
.margin-l-5 {margin-left: 5px;}
.margin-l-10 {margin-left: 10px;}
.margin-l-15 {margin-left: 15px;}
.margin-l-20 {margin-left: 20px;}
/*右外边距*/
.margin-r-0 {margin-right: 0 !important;}
.margin-r-5 {margin-right: 5px;}
.margin-r-10 {margin-right: 10px;}
.margin-r-15 {margin-right: 15px;}
.margin-r-20 {margin-right: 20px;}

/*上内边距*/
.padding-t-5 {padding-top: 5px;}
.padding-t-10 {padding-top: 10px;}
.padding-t-15 {padding-top: 15px;}
.padding-t-20 {padding-top: 20px;}
/*下内边距*/
.padding-b-5 {padding-bottom: 5px;}
.padding-b-10 {padding-bottom: 10px;}
.padding-b-15 {padding-bottom: 15px;}
.padding-b-20 {padding-bottom: 20px;}
/*左内边距*/
.padding-l-0 {padding-left:0 !important;}
.padding-l-5 {padding-left:5px;}
.padding-l-10 {padding-left:10px;}
.padding-l-15 {padding-left:15px;}
.padding-l-20 {padding-left: 20px;}
/*右内边距*/
.padding-r-0 {padding-right:0 !important;}
.padding-r-5 {padding-right: 5px;}
.padding-r-10 {padding-right: 10px;}
.padding-r-15 {padding-right: 15px;}
.padding-r-20 {padding-right: 20px;}

/*相对定位*/
.rlt{position: relative;}
/*绝对定位*/
.abs{position: absolute;}

/*两端对齐居中*/
.k-justify-flex {
	width: 100%;
	display: -webkit-box;
    -webkit-box-pack: justify;
    -webkit-box-align: center;
}

/*有时候在用框架的ui-borderd有问题时用自定义的k-border*/
.k-border {border: 1px solid #e0e0e0}

/*重置框架头像的默认背景图*/
.ui-avatar,
.ui-avatar-s,
.ui-avatar-lg {background-image: none;}

/*自定义列表的样式，用时和框架的ui-list结合使用*/
.k-list>li {
	padding-right: 15px;
	padding-left: 15px;
	margin-left: 0;
	-webkit-box-align: center;
}
@media (max-width: 320px){
	.k-list>li {
	    padding-right: 10px;
		padding-left: 10px;
	}
}

/*自定义列表选项的样式，用时和框架的ui-list结合使用*/
.k-list-link>li{
    padding-right: 30px;
    -webkit-box-align: center;
}
/*自定义列表箭头和带箭头的表单选项*/
.k-list-link>li:after,
.k-form-item-link:after {
    font-family:"k-iconfont" !important;
    font-size: 20px;
    line-height: 44px;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: .2px;
    display: block;
    color: rgba(0,0,0,.5);
    color: #c7c7c7;
    content: "\e612";
    position: absolute;
    right: 15px;
    top: 50%;
    margin-top: -21px;
    margin-right: -10px;
}


/*flex-box左右和垂直居中,高度需要自己定义*/
.k-box-center {
	display: -webkit-box;
	width: 100%;
    -webkit-box-flex: 1;
    -webkit-box-sizing: border-box;
    -webkit-box-orient: vertical;
    -webkit-box-pack: center;
    -webkit-box-align: center;
}
/*flex-box左右居中,高度需要自己定义*/
.k-box-pack {
	display: -webkit-box;
	width: 100%;
    -webkit-box-flex: 1;
    -webkit-box-sizing: border-box;
    -webkit-box-pack: center;
}
/*flex-box垂直居中,高度需要自己定义*/
.k-box-align {
	display: -webkit-box;
	width: 100%;
    -webkit-box-flex: 1;
    -webkit-box-sizing: border-box;
    -webkit-box-align: center;
}

/******添加链接箭头******/
.k-link:after {
    font-family:"k-iconfont" !important;
    font-size: 20px;
    line-height: 44px;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: .2px;
    display: block;
    color: rgba(0,0,0,.5);
    color: #c7c7c7;
    content: "\e612";
    position: absolute;
    right: 15px;
    top: 50%;
    margin-top: -22px;
    margin-right: -10px;
}

/*不可用时的样式，如果不是在button或表单元素上，那仅仅是样式改变不是真正的不可用，禁用需要自己用js控制*/
[disabled]
.disabled {
	color: #fff;
	background: #e9ebec;
	-webkit-background-clip: padding-box;
	background-clip: padding-box;
}

/*自定义文字截断行数 ，截取两行或单行用框架定义好的*/
.line-clamp {
	display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
}
.line-clamp-3 {	
    -webkit-line-clamp: 3;
}
.line-clamp-4 {	
    -webkit-line-clamp: 4;
}

